<template id="added-days-template">
  <ul class="list-unstyled">
    <li class="clearfix" v-for="(day, key) in list" :key="key">
      {{ day.dayName }} from
      {{ day.openingTimeName }} to
      {{ day.closingTimeName }}
      <button
        class="btn btn-danger btn-sm pull-right"
        @click.prevent="removeDay(day)"
      >Remove</button>
    </li>
  </ul>
</template>

<script>
export default {
  name: 'days',
  props: {
    list: Array
  },
  methods: {
    removeDay(day) {
      this.$emit('remove', day)
    }
  }
}
</script>



